<template>
  <view class="custom-tabbar">
    <view
      v-for="(item, index) in tabList"
      :key="index"
      class="tab-item"
      :class="{ active: currentPage === item.pagePath }"
      @click="switchTab(item.pagePath)"
    >
      <image
        class="tab-icon"
        :src="currentPage === item.pagePath ? item.selectedIconPath : item.iconPath"
      />
    </view>
  </view>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'

const tabList = [
  {
    pagePath: '/pages/index/index',
    iconPath: '/static/images/home.png',
    selectedIconPath: '/static/images/homefill2.png'
  },
  {
    pagePath: '/pages/previewList/previewList',
    iconPath: '/static/images/worker.png',
    selectedIconPath: '/static/images/workerfill.png'
  },
  {
    pagePath: '/pages/chatList/chatList',
    iconPath: '/static/images/message.png',
    selectedIconPath: '/static/images/messagefill.png'
  },
  {
    pagePath: '/pages/profile/profile',
    iconPath: '/static/images/我的.png',
    selectedIconPath: '/static/images/我的2.png'
  }
]

const currentPage = ref('')

// 初始化：判断当前路由
onMounted(() => {
  const pages = getCurrentPages()
  const current = pages[pages.length - 1]
  currentPage.value = '/' + current.route
})

// 切换页面
function switchTab(url) {
  if (currentPage.value !== url) {
    uni.switchTab({ url })
  }
}
</script>

