  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100rpx;
  background-color: #ffffff;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 1rpx solid #eee;
  z-index: 999;
}

.tab-item {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab-icon {
  width: 48rpx;
  height: 48rpx;
}

.tab-item.active .tab-icon {
  /* 可选：加入选中时的放大或颜色效果 */
}
</style>
